.container {
  .hamburger {
    position: fixed;
    top: 20px;
    right: 50px;
    z-index: 3;
    cursor: pointer;
  }

  .login {
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 60% 40%;

    .background {
      grid-column: 1;
      position: relative;
      height: 100%;
      width: 100%;
      background: linear-gradient(
          0deg,
          rgba(252, 249, 234, 0.5),
          rgba(252, 249, 234, 0.5)
        ),
        url("./images/background2.jpg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;

      .blur {
        height: 100%;
        width: 100%;
        background: linear-gradient(
            0deg,
            rgba(252, 249, 234, 0.5),
            rgba(252, 249, 234, 0.5)
          ),
          url("./images/background2.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        filter: blur(1px);
      }
    }

    .panel {
      color: #305f72;
      background: #ffffff;
      grid-column: 2;
      z-index: 2;

      .input {
        width: 100%;
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -60%);
        text-align: center;

        .text {
          font-size: 52px;
          font-weight: 800;
          margin-bottom: 45px;
        }

        .error {
          width: 100%;
          height: 50px;

          &.failed {
            color: rgb(248, 70, 70);
          }
          &.success {
            color: rgb(79, 214, 108);
          }
        }

        .username {
          height: 50px;
          width: 60%;
          margin: 0 auto 25px auto;
          box-shadow: -9px -9px 12px rgba(255, 255, 255, 0.5),
            12px 12px 16px rgba(188, 206, 216, 0.5);
          border-radius: 5px;
          padding: 0 20px;
          outline: none;
          border: none;
          background-color: #ffffff;
          color: #305f72;
          font-size: 13px;
          font-weight: 600;
        }

        .password {
          height: 50px;
          width: 60%;
          margin: 0 auto 25px auto;
          box-shadow: -9px -9px 12px rgba(255, 255, 255, 0.5),
            12px 12px 16px rgba(188, 206, 216, 0.5);
          border-radius: 5px;
          padding: 0 20px;
          outline: none;
          border: none;
          background-color: #ffffff;
          color: #305f72;
          font-size: 13px;
          font-weight: 600;
        }

        .button {
          background: #305f72;
          box-shadow: -9px -9px 12px rgba(255, 255, 255, 0.5),
            9px 9px 16px rgba(48, 95, 114, 0.5);
          border-radius: 5px;
          margin: 20px auto 5px auto;
          height: 50px;
          width: 60%;
          font-size: 18px;
          font-weight: 600;
          cursor: pointer;
          text-decoration: none;
          transition: background 0.8s;

          div {
            position: relative;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-weight: 800;
            font-size: 18px;
            line-height: 30px;
            color: #ffffff;
          }

          &:hover {
            background: #41819a
              radial-gradient(circle, transparent 1%, #41819a 1%) center/15000%;
          }
        }
        .a {
          text-decoration: none;
        }

        .link {
          font-size: 12px;
          font-weight: 300px;
          margin: 10px;

          .a {
            color: #305f72;
            span {
              padding: 5px;
              font-weight: 600;
              text-decoration: underline;
              cursor: pointer;
            }
          }
          &:hover {
            color: #41819a;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 750px) {
  .container {
    .hamburger {
      right: 20px;
    }

    .login {
      grid-template-columns: 100%;

      .background {
        display: none;
      }

      .panel {
        grid-column: 1;

        .input {
          .text {
            font-size: 38px;
          }
        }
      }
    }
  }
}
